ACA,WAAA;EACA,0BAAA;EACA,4BAAA;EACA,0BAAA;EACA,cAAA;AAwGJ;AAjGE;EAtBF;IAuBI,cAAA;IACA,eAAA;EAoGF;AACF;AAlGE;EACE,2BAAA;EACA,0CAAA;EACA,+BAAA;AAoGJ;;AAhGA;EACE,mBAAA;EACA,0BAAA;EACA,4BAAA;EAEA,eAAA;EACA,0BAAA;AAkGF;;AA9FA;EACE,kBAAA;EACA,yBAAA;EACA,iBAAA;EACA,WAAA;EACA,kBAAA;EACA,kBAAA;AAiGF;AA/FE;EACE,kBAAA;EACA,cAteS;EAueT,gCAAA;AAiGJ;AA9FE;EACE,aAAA;EACA,gCAAA;AAgGJ;;AA5FA;EACE,aAAA;EACA,WAAA;EACA,gBAAA;EACA,0BAAA;EACA,gGAAA;AA+FF;;AA3FE;EACE,aAAA;EACA,kBAAA;AA8FJ;AA5FI;EACE,iBAAA;EACA,kBAAA;EACA,cAhgBO;AA8lBb;AA3FI;EACE,iBAAA;EACA,gCAAA;EACA,gBAAA;EACA,kBAAA;AA6FN;;AAtFA;EACE,+DAAA;EACA,eAAA;EACA,cAAA;EACA,iBAAA;EACA,+BAAA;EACA,kBAlhBc;AA2mBhB;AAvFE;EACE,gBAAA;AAyFJ","sourcesContent":["\n// Font imports\n@font-face {\n  font-family: 'alagard';\n  src: url('../fonts/alagard.ttf') format('truetype');\n  font-weight: 600;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'monaco';\n  src: url('../fonts/monaco.ttf') format('truetype');\n  font-weight: 600;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'RobotoMono';\n  src: url('../fonts/RobotoMono-Light.ttf') format('truetype');\n  font-weight: 600;\n  font-style: normal;\n}\n","@use \"sass:color\";\n@use 'sass:math' as math;\n@use 'fonts' as *;\n\n// Variables\n$header-font: 'monaco';\n$body-font: 'monaco';\n$description-font:  'monaco'; //'RobotoMono', monospace;\n$accent-color: #9eff4a; // Adjusted to a softer yellow-green that matches the monster\n\n$text-light: #FFFFFF;\n$card-bg: rgba(0, 0, 0, 0.45);\n$border-radius: 8px;\n$page-bg: #2b222a;\n\n// Base styles\nhtml {\n  font-size: 16px; // Base size to calculate rem from\n  min-width: 100vw;\n  margin: 0;\n  padding: 0;\n  background-color: $page-bg;\n\n  text-rendering: optimizeSpeed;\n  font-smooth: never;\n  -webkit-font-smoothing: none;\n  overflow-x: hidden; /* Prevent horizontal scrolling */\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  font-size: clamp(2rem, 5vw, 4rem);\n  line-height: 1.0;\n  font-family: $body-font;\n  font-weight: normal;\n  color: $text-light;\n\n  //max-width: 1200px;\n  //margin: 25px auto;\n  //padding: 0 20px;\n\n  letter-spacing: 0rem;\n  width: 100%; /* Explicit width */\n  max-width: 100vw; /* Don't exceed viewport */\n  overflow-x: hidden; /* Hide horizontal overflow */\n  box-sizing: border-box; /* Include padding in width calculation */\n}\n\n// Typography\nh1, h2, h3, h4, h5, h6 {\n  line-height: 1;\n  text-rendering: optimizeSpeed;\n  font-smooth: never;\n  -webkit-font-smoothing: none;\n  margin-top: 0;\n  margin-bottom: 2rem;\n  font-weight: normal;\n  font-family: $header-font;\n  color: $text-light;\n}\n\nh1 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\nh2 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\nh3 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\nh4 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\nh5 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\nh6 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n//\n//.pixel-perfect-text {\n//  font-size: 64px; // Base font size (usually pixel font's design size)\n//  // Use math.div() instead of / operator\n//  $base-width: 320px;\n//  $scale-factor: math.floor(math.div(100vw, $base-width));\n//  $scale-factor: math.max(1, $scale-factor);\n//\n//  transform-origin: left top;\n//  transform: scale($scale-factor);\n//}\n\n@mixin pixelated-font {\n  font-smooth: never;\n  -webkit-font-smoothing: none;\n  -moz-osx-font-smoothing: grayscale;\n  font-variant-ligatures: none;\n  text-rendering: optimizeSpeed;\n  letter-spacing: 0;\n}\n\n\n// Pixel-perfect text mixin\n@mixin pixel-text-base($size) {\n  font-size: $size + px;\n  $base-width: 320px;\n  $scale-factor: math.floor(math.div(100vw, $base-width));\n  $scale-factor: math.max(1, $scale-factor);\n\n  transform-origin: left top;\n  transform: scale($scale-factor);\n\n  @include pixelated-font;\n}\n\n\n// Generate pixel text classes with different sizes\n$pixel-text-sizes: 16, 24, 32, 48, 64, 80, 96, 114;\n\n@each $size in $pixel-text-sizes {\n  .pixel-text-#{$size} {\n    @include pixel-text-base($size);\n  }\n}\n//\n//// Apply to all text elements\n//h1, h2, h3, h4, h5, h6, p, div, span {\n//  @include pixelated-font;\n//}\n\n\n\n// Create a proper grid layout\n@media (min-width: 2001px) {\n  .pixel-perfect-text-large {\n    @include pixel-text-base(114);\n  }\n  .pixel-perfect-text-medium {\n    @include pixel-text-base(64);\n  }\n}\n\n@media (min-width: 1501px) and (max-width: 2000px) {\n  .pixel-perfect-text-large {\n    @include pixel-text-base(96);\n  }\n  .pixel-perfect-text-medium {\n    @include pixel-text-base(64);\n  }\n}\n\n@media (min-width: 769px) and (max-width: 1500px) {\n  .pixel-perfect-text-large {\n    @include pixel-text-base(64);\n  }\n  .pixel-perfect-text-medium {\n    @include pixel-text-base(48);\n  }\n}\n\n@media (max-width: 768px) {\n  .pixel-perfect-text-large {\n    @include pixel-text-base(32);\n\n  }\n  .pixel-perfect-text-medium {\n    @include pixel-text-base(24);\n  }\n}\n\n\n\n// Marketing section styling\n.marketing-content {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n}\n\n.tagline {\n  margin-bottom: 20px;\n  color: $accent-color;\n}\n\n.game-description {\n  margin: 20px 0;\n  max-width: 800px;\n}\n\n.cta-button {\n  margin-top: 20px;\n  padding: 10px 20px;\n  background-color: $accent-color;\n  color: #000;\n  border-radius: 4px;\n  display: inline-block;\n  transition: background-color 0.3s ease;\n\n  &:hover {\n    background-color: color.adjust($accent-color, $lightness: -20%);\n  }\n}\n\n// Links\na {\n  font-weight: normal;\n  text-decoration: none;\n  color: $accent-color;\n\n  &:focus {\n    outline: thin dotted;\n    color: color.adjust($accent-color, $lightness: 10%);\n  }\n}\n\nheader {\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  width: 100%;\n  max-width: 100vw;\n  box-sizing: border-box;\n  overflow: hidden;\n}\nfooter {\n  background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  width: 100%;\n  max-width: 100vw;\n  box-sizing: border-box;\n  overflow: hidden;\n}\n\n// Adjust content container width for larger screens\nbody {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  max-width: 2400px; // Increased to match gallery container\n  margin: 25px auto;\n  padding: 0 30px; // Increased padding for larger screens\n\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) {\n    max-width: 2400px;\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    max-width: 1400px;\n  }\n\n  @media (min-width: 769px) and (max-width: 1500px) {\n    max-width: 800px;\n  }\n\n  @media (max-width: 768px) {\n    max-width: 700px;\n    padding: 0 15px;\n  }\n}\n\n/* Keep your original CSS exactly as is */\n.cropped-image {\n  margin: 0px;\n  padding: 0px;\n  width: 100%; /* Container width */\n  height: 300px; /* Fixed height */\n  overflow: hidden; /* Hide parts outside container */\n  /* NO position: relative here */\n  //position: relative;\n\n  /* Safari-specific orientation change fix */\n  @supports (-webkit-touch-callout: none) {\n    width: 100vw; /* Use viewport width instead of percentage */\n    margin-left: calc(-50vw + 50%); /* Center relative to the body */\n    box-sizing: border-box;\n  }\n}\n\n.cropped-image img {\n  /* Center the image horizontally */\n  position: absolute;\n  left: 50%;\n  transform: translateX(-50%);\n  /* Optional: adjust vertical position */\n  top: 0;\n  max-width: none;     /* IMPORTANT: Prevents the image from resizing to fit container */\n  height: 100%;\n  width: auto;\n  z-index: -1; /* Place behind all other content */\n\n\n  /* Add Safari-specific orientation change fix */\n  @supports (-webkit-touch-callout: none) {\n    /* Ensure image covers the full width during rotation */\n    min-width: 100vw;\n    object-fit: cover;\n    object-position: top center;\n  }\n}\n\n/* Add only these Safari-specific fixes */\n@supports (-webkit-touch-callout: none) {\n  /* Only fix the container horizontal overflow in Safari */\n  header, #header {\n    //max-width: 100vw;\n    //overflow-x: hidden;\n  }\n}\n\n.banner-container {\n  width: 100%;\n  //max-width: 2400px; // Increased for large monitors\n  margin: 0 auto;\n}\n\n/* Refactor your banner-image class in main.scss */\n.banner-image {\n  /* Keep existing pixel-perfect rendering properties */\n  image-rendering: pixelated;\n  image-rendering: -moz-crisp-edges;\n  image-rendering: crisp-edges;\n  -ms-interpolation-mode: nearest-neighbor; /* IE */\n\n  /* Center the image */\n  display: block;\n  margin: 0 auto;\n\n  /* Don't set max-width to allow for exact integer scaling */\n  /* max-width: 100%; */ /* Remove this line */\n  height: auto;\n\n  /* Keep transform origin */\n  transform-origin: center;\n\n  /* Keep your original styles */\n  border-radius: $border-radius $border-radius 0 0;\n\n  /* Add Safari-specific rendering */\n  @supports (-webkit-touch-callout: none) {\n    transform: translateZ(0); /* Helps with Safari rendering */\n  }\n}\n\n.image-placeholder {\n  //margin-top: calc(100vh * 0.3552 + 47.6051px);\n  margin-bottom: 450px; /* More specific and forceful for testing */\n\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    margin-bottom: 650px;\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    margin-bottom: 650px;\n  }\n\n  @media (min-width: 769px) and (max-width: 1500px) { // 2 items per row\n    margin-bottom: 400px;\n  }\n\n  @media (max-width: 768px) {\n    margin-bottom: 175px;\n  }\n\n}\n\n// Content sections\n.content {\n  background: $card-bg;\n  padding: 25px 25px 25px 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#fullpage {\n  display: none;\n  position: fixed;\n  z-index: 9998;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: rgba(0, 0, 0, 0.85); /* Add overlay background */\n  opacity: 0;\n  transition: opacity 0.3s ease-in-out;\n\n  .fullpage-image {\n    width: 90%; /* Reduced from 100% to create margin */\n    height: 90%; /* Reduced from 100% to create margin */\n    background-size: contain;\n    background-repeat: no-repeat;\n    background-position: center center;\n    background-color: transparent;\n    image-rendering: pixelated;\n    image-rendering: crisp-edges;\n    //transform: scale(1); /* Integer scaling for pixel art */\n    transform-origin: center center;\n    margin: 5vh auto; /* Add vertical and horizontal margin */\n  }\n}\n\n// Gallery styles\n.gallery-container {\n  width: 100%;\n  //max-width: 2400px; // Increased for large monitors\n  margin: 0 auto;\n}\n\n.artwork-grid {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 20px;\n  justify-content: center;\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(25% - 15px);\n      max-width: calc(25% - 15px);\n    }\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(33.333% - 14px);\n      max-width: calc(33.333% - 14px);\n    }\n  }\n\n  @media (min-width: 764px) and (max-width: 1500px) { // 2 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(50% - 10px);\n      max-width: calc(50% - 10px);\n    }\n  }\n}\n\n.artwork-item {\n  border-radius: $border-radius;\n  box-shadow: 0 4px 12px color.adjust($card-bg, $alpha: -0.5);\n  transition: transform 0.3s ease, bo